<template>
  <div>
    <div>
      <!-- 搜索筛选区 -->
      <el-row class="serach-box">
        <el-col v-for="item in headerItems" :key="item" :span="4">
          <el-col :span="7">{{item.label}}</el-col>
          <el-col :span="16"><el-input v-model="userQuery[item.prop]" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
          <el-col>
          </el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">状态</el-col>
          <el-col :span="16">
            <el-select
            class="status-select"
            v-model="userQuery.planState"
            placeholder="请选择模板"
            @change="query"
          >
            <el-option
              v-for="item in statusTypeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select>
          </el-col>
        </el-col>
        <el-col :span="4">
          <el-date-picker
            v-model="dataValue"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <!-- 按钮区 -->
      <el-row class="serach-box">
        <el-col :span="4">
          <el-col :span="7">批量更新</el-col>
          <el-col :span="16">
            <el-select v-model="selectStatus" placeholder="请选择模板">
            <el-option
              v-for="item in statusChangeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select>
          </el-col>
        </el-col>
        <el-col :span="8" class="btn-area">
        <el-col :span="6">
          <el-button type="info" @click="toSample">更新状态</el-button>
        </el-col>
        <el-col :span="6">
          <el-button type="info" @click="specificationDialogDisplay = true"
            >导出Excel</el-button
          >
        </el-col>
        <el-col :span="6" type="primary">
          <el-button type="info" @click="toAdd">新增</el-button>
        </el-col>
        <el-col :span="6">
          <el-button  type="danger" @click="removePreview"
            >删除</el-button
          >
        </el-col>
        </el-col>
      </el-row>
      <!-- 日期筛选区 -->
      <el-row class="serach-box">
        <el-col class="status-area" :span="6">
          <span>已完成:{{ finished }}</span>
          <span>未完成:{{ notFinished }}</span>
          <span>修改中:{{ modified }}</span>
          <span>取消:{{ cancel }}</span>
        </el-col>
      </el-row>
      <!-- 表单区 -->
      <el-table @select="rowclick" :data="tableData" border style="width: 100%">
        <el-table-column type="selection" fixed width="55"> </el-table-column>
        <el-table-column type="index" fixed width="55" label="序号"> </el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList1"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column label="照片" width="200">
          <template slot-scope="scope">
            <el-image
              style="width: 60px; height: 60px; line-height: 60px"
              :src="scope.row.url"
              :preview-src-list="srcList"
            >
            </el-image>
            <!-- <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="" />
            </el-dialog> -->
          </template>
        </el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList2"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 删除对话框 -->
      <el-dialog
        class="summary"
        width="35%"
        title="删除确认"
        :visible.sync="deleteDialogDisplay"
      >
        <el-row>将从系统中删除以下样品信息：</el-row>
        <div class="infor">
          <div v-for="item in selectData" :key="item.index">
            {{ item.orderId }}
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="deleteDialogDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary"  @click="remove">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  GetAllMultiSizePlans,
  DeleteMultiSizePlan,
} from "network/newMulti.js";

import { GetCommonFormState } from "network/common.js";
export default {
  data() {
    return {
      selectData: [],
      deleteDialogDisplay: false,
      dataValue: null,
      totalAmount: null,
      finished: null,
      notFinished: null,
      modified: null,
      cancel: null,
      selectStatus:null,
      statusTypeList: [],
      statusChangeList: [],
      headerItems:[{
        label:"订单号",
        prop:"orderId"
      },{
        label:"业务员",
        prop:"salesMan"
      },{
        label:"客户",
        prop:"customer"
      },{
        label:"货号",
        prop:"productId"
      }],
      tableData: [
      ],
      tableList1:[
        { label: "订单号", prop: "orderId", width: "150", isFix: "left" },
        { label: "业务员", prop: "salesManName" },
        { label: "客户", prop: "customerName" },
        { label: "样品号", prop: "sampleNumber", width: "150"},
        { label: "货号", prop: "productId", width: "150"},
        { label: "品名", prop: "productName", width: "150"},
        { label: "花型描述", prop: "patternDescription" , width: "150"},
        { label: "色调", prop: "hue" , width: "150"}
      ],
      tableList2:[
        { label: "总数", prop: "totalAmount" },
        { label: "制单日期", prop: "creationTime" , width: "150"},
        { label: "状态", prop: "planState", isFix: "right" },
      ],
      deleteDialogDisplay: false,
      userQuery: {
        orderId: "",
        salesMan: "",
        customer: "",
        productId: "",
        planState: "完成",
        beginTime: "2021-01-01",
        endTime: "2021-01-30",
        pageSize: 10,
        pageNumber: 1,
      },
    };
  },
  created() {
    this.inital();
  },
  watch: {
    dataValue(val) {
      this.userQuery.beginTime = val[0];
      this.userQuery.endTime = val[1];
      this.query();
    },
  },
  methods: {
    //初始化
    inital() {
      this.getMultiSizeList(this.userQuery);
      this.getStatusList();
    },
    //获得表状态列表
    getStatusList() {
      GetCommonFormState().then((res) => {
        if (res.success) {
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      });
    },
    //获得计划表均码列表
    getMultiSizeList(data){
      GetAllMultiSizePlans(data).then(res=>{
        if(res.success){
          this.tableData = res.result.planDto;
          this.totalAmount = res.result.totalAmount;
          this.finished = res.result.finished;
          this.notFinished = res.result.notFinished;
          this.modified = res.result.modified;
          this.cancel = res.result.cancel;
        }
      })
    },
    //点击查询
    query() {
      this.getMultiSizeList(this.userQuery);
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //编辑
    edit(scope){
       this.$router.push({
        path:"/newMulti",
        query:{
          iid:scope.row.id
        }
      })
    },
    //删除预览
    removePreview(){
      if(this.selectData.length!=0){
        this.deleteDialogDisplay = true;
      }
      else{
        this.$message.error("请先选择要删除的项！")
      }
      
    },
    //批量删除
    remove() {
      var data = this.selectData;
      if (data.length == 0) {
        this.$message.error("请先选择项！");
      } else {
        let arr = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.id;
        });
        DeleteMultiSizePlan(arr).then((res) => {
          if (res.success) {
            this.$message.success("删除成功");
          }
        });
      }
    },
    toAdd() {
      this.$router.push("/newMulti");
    },
  },
};
</script>

<style scoped>
.status-area span {
  line-height: 40px;
  margin-right: 20px;
}
.el-row {
  margin-bottom: 10px;
}
.btn-area .el-button{
  width:100px;
}
</style>